.popup {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px;
  background: rgba(0,0,0, .8);
  border-radius: 10px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  z-index: 1;

  &__title {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 32px;
    margin-bottom: 40px;
  }
  &__item {
    margin-bottom: 25px;
    font-size: 20px;
  }
  &__name {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 18px;
  }
}