* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  outline: 0;
  font-family: 'Roboto', -apple-system, system-ui, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background: #F9F9FB;
  font-size: 1rem;

  padding: 0 25px;
}

#root {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
}

.quiz {
  position: relative;
  padding: 10px 25px 25px;
  width: 680px;
  min-height: 500px;
  margin: 0 auto;
  border: 1px solid #F1F1F1;
  border-radius: 10px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  background: #fff;
  box-shadow: 0px 0px 13px 0px rgba(0,0,0, .100);
  &__body {
    margin-top: 40px;
  }
}

.btn-default {
  font-family: 'Montserrat', sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  margin: 0 auto;
  color: #fff;
  border: 0;
  cursor: pointer;
  outline: none;
  touch-action: manipulation;
  transition: background-color .3s ease,box-shadow .3s ease,color .3s ease,transform .3s ease,opacity .3s ease;
  font-size: 1.2rem;
  letter-spacing: .05em;
  font-weight: 600;
  border-radius: 8px;
  padding: 15px 28px;
  background-color: #d56b50;
  text-transform: uppercase;
  &:hover {
    background-color: darken(#d56b50, 2%);
  }
  &:active {
    background-color: darken(#d56b50, 7%);
  }
}

.logo {
  display: flex;
  align-items: center;

  span {
    font-size: 1.1rem;
    font-weight: 500;
    opacity: .5;
    display: block;
    margin-top: 5px;
    margin-left: -6px;
  }
}

h1 {
  font-size: 1.7rem;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  opacity: .7;
  font-weight: 400;
}