.question {
  &__name {
    font-size: 1.7rem;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    margin-bottom: 30px;
    opacity: .7;
  }

  &.entering {
    animation: fade-in .5s forwards;
  }

  &.exiting {
    animation: fade-in .5s forwards;
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}