.variants {
  &__item {
    font-size: 1.2rem;
    font-weight: 400;
    color: darken(#c9ccd9, 30%);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    min-height: 72px;
    height: 100%;
    width: 100%;
    padding: 12px;
    border: 1px solid #c9ccd9;
    border-radius: 8px;
    transition: .3s;
    margin-bottom: 20px;
    cursor: pointer;
    &:hover {
      box-shadow: 0 4px 32px #e4e6ec
    }
  }
}