.results {
  text-align: center;


  &__img {
    max-width: 100%;
    width: 420px;
    margin-bottom: 20px;
  }

  &__text {
    margin-bottom: 20px;
  }

  &__btn {
    max-width: 360px;
  }

  p {
    margin-top: 10px;
    font-size: 1.2rem;
    opacity: .6;
  }

  &__btn {
    position: absolute;
    bottom: 10px;
    right: 10px;
    z-index: 2;
    color: #ffffff;
    width: 30px;
    height: 30px;
    background: rgba(0,0,0, .7);
    border: none;
    border-radius: 50%;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
  }
}

